@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,300;0,400;0,600;0,700;0,800;1,700;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background-color: transparent;
}



.View{display:flex;width:100%;height:120vh}

.Decorate{background-color:#D7D7D7;width:50vw;height:100%;padding-bottom:10vh;display:flex;flex-direction:column;justify-content:center;align-items:center}@media (max-width: 768px){.Decorate{background-color:#D7D7D7;padding:0;width:100%;height:35vh;text-align:center}}.Decorate__image{max-width:600px}@media (max-width: 768px){.Decorate__image{max-width:200px;height:auto}}

.Header{text-align:center;font-size:36px;font-family:"Nunito", sans-serif;font-weight:700;margin:48px 0 0 0}@media (max-width: 768px){.Header{font-size:20px;margin:96px 0 0 0}}.Header__w{background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Content{display:flex;flex-direction:column;justify-content:center;align-items:center;background-color:#171820;color:white;padding-bottom:10vh;width:50vw}@media (max-width: 768px){.Content{width:100%;height:65vh;padding:0 16px}}.Content__header{margin:0 0 48px 0;font-size:48px;font-weight:700;font-style:italic;text-align:center;max-width:600px;font-family:'Nunito', sans-serif}@media (max-width: 768px){.Content__header{font-size:24px;max-width:200px;margin:0 0 24px 0}}.Content__paragraph{text-align:center;margin:0 0 80px 0;font-size:16px;font-weight:300;line-height:42px;max-width:600px}@media (max-width: 768px){.Content__paragraph{font-size:12px;margin:0 0 24px 0;max-width:272px;line-height:20px}}.Content__span{color:#FFE321;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Slider{font-weight:800;font-style:italic;font-family:"Nunito", sans-serif;color:white;text-align:center;font-size:72px}.Slider__yellow{color:#FFE321;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Button{width:192px;height:48px;border-radius:16px;font-size:24px;font-weight:600;font-family:'Nunito', sans-serif;background-color:#FFE321;box-shadow:0 0 10px rgba(0,0,0,0.25);border:none}@media (max-width: 768px){.Button{width:128px;height:36px;font-size:20px}}.Button:focus{outline:1px solid #171820}

.Contact{display:flex;justify-content:space-around;align-items:center;width:100%;height:60vh;background-color:#171820}.Contact__container{text-align:center}.Contact__header{font-family:'Nunito', sans-serif;font-size:48px;font-style:italic;color:white;margin:0 0 72px 0}.Contact__link{display:block;font-size:48px;font-weight:700;text-decoration:none;margin:0 0 48px 0;color:#FFE321;background:linear-gradient(180deg, #FFE321 0%, #F5C001 85.94%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;-webkit-box-decoration-break:clone;box-decoration-break:clone}

.Form{color:white;display:flex;flex-direction:column;align-items:center}.Form__header{font-family:'Nunito', sans-serif;font-size:48px;font-weight:700;font-style:italic;margin:0 0 32px 0;text-align:center}.Form__container{display:flex;margin-bottom:16px}.Form__box{background-color:#C4C4C4;border:2px solid #FFE321;width:240px;height:50px;border-radius:16px;display:flex;align-items:center}.Form__box--topic{margin-right:24px}.Form__label{font-size:12px;font-weight:600;padding-left:20px;padding-right:4px;color:rgba(23,24,32,0.5)}.Form__input{background-color:#C4C4C4;border:none;width:144px}.Form__input:focus{outline:1px solid #171820}.Form__textarea{width:500px;height:132px;box-sizing:border-box;background-color:#C4C4C4;border-radius:16px;border:2px solid #FFE321;padding:8px;margin-bottom:16px;font-size:16px}.Form__textarea::-webkit-input-placeholder{font-size:16px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__textarea:-ms-input-placeholder{font-size:16px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__textarea::-ms-input-placeholder{font-size:16px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__textarea::placeholder{font-size:16px;font-weight:600;color:rgba(23,24,32,0.5);font-family:"Nunito", sans-serif}.Form__textarea-fieldset{border:none;margin:0;padding:0}.Form__button-box{align-self:flex-start}

.Banner{background-color:#FFE321;display:flex;justify-content:space-around;align-items:center;height:16vh;width:100%}.Banner__paragraph{color:#171820;font-size:18px;font-weight:600;font-style:italic}.Banner__bar{font-size:18px;color:#171820;border:2px solid #171820;border-radius:4px;width:280px;display:flex;align-items:center;justify-content:space-around}.Banner__icon{width:32px;height:32px}

.Navigation{width:100%;height:48px;position:fixed;top:0;left:0;z-index:10}.Navigation__nav{width:100%;height:48px;display:flex;align-items:center}.Navigation__left{height:100%;width:50%;background-color:rgba(215,215,215,0.8);display:flex;justify-content:flex-start;align-items:center}.Navigation__home{margin-left:32px}.Navigation__image{width:auto;height:44px}.Navigation__right{height:100%;width:50%;background-color:rgba(23,24,32,0.8);display:flex;justify-content:flex-end;align-items:center}.Navigation__list{padding:0;margin:0;height:100%;width:100%;display:flex;justify-content:flex-end;align-items:center}.Navigation__item{list-style:none;margin-right:48px;display:flex;flex-direction:column;justify-content:center}.Navigation__item--offer{position:relative}.Navigation__item--offer:hover{height:176px}.Navigation__item--offer:hover .Navigation__sublist{display:block;z-index:1}.Navigation__item--subitem{margin:16px 0 0 0;text-align:center}.Navigation__sublist{display:none;padding:0;margin:0;position:absolute;top:112px;left:-18px;background-color:rgba(23,24,32,0.8)}.Navigation__link{font-size:16px;font-weight:800;color:#D7D7D7;text-decoration:none}.Navigation__link--sublink{margin:0;font-size:14px;line-height:22px}


